import $ from 'jquery'
import kjvBot from '../kjvbot'

const Sidenav = () => {
  const toggleaudioEnabled = function(e)
  {
    var $this =  $('.audioEnabledToggle').find('.switch input');
    $this.prop('checked', !$this.prop('checked'));
    var checked = $this.prop('checked');
    kjvBot.setOption('audioEnabled', checked);
    kjvBot.saveState();
    if (checked) kjvBot.speak('Audio announcements enabled.');
    e.preventDefault();
  }

  return (
    <ul id="slide-out" className="sidenav">
      <li>
        &nbsp;
      </li>
      <li>
        <hr />
      </li>
      <li>
        <a
          target="_blank"
          href="https://virtualimmortal.github.io/kjvbot/landings/embed-demo/template1.htm"
          className="btn"
        >
          <i className="far fa-eye"></i>
          Embed Demo
        </a>
      </li>
      <li>
        <a
          title="Open kjvBot code page on github"
          href="https://github.com/Virtualimmortal/kjvBot/tree/master"
          target="_blank"
          className="btn"
        >
          <i className="fa fa-code"></i>
          Code
        </a>
      </li>
      <li className="no-padding"></li>
      <li>
        <hr />
      </li>
      <li className="no-padding">
        <a 
        className="audioEnabledToggle"
        onClick={(e)=> toggleaudioEnabled(e)}

        >
            Audio
            <div className="switch right">
                <label>
                    Off
                    <input 
                      type="checkbox" 
                      defaultChecked={kjvBot.getOption('audioEnabled')}

                      >
                    </input>
                    <span className="lever "></span>
                    On
                </label>
            </div>
        </a>
    </li>
      <li className="no-padding">
        <a className="resetStateBtn" href="/">
          <i className="material-icons">refresh</i>Reset
        </a>
      </li>
    </ul>
  );
};

export default Sidenav;
