import $ from 'jquery'
import M from 'materialize-css'

const FloatingActionMenu = () => {

  return (
    <div id="floatingActionMenu" className="FloatingActionMenu fixed-action-btn">
        <a className="btn-floating btn-large grey darken-3">
          <i className="fa fa-ellipsis-v"></i>
        </a>
        <ul>
          <li>
            <a
              title="Open kjvBot code page on github"
              href="https://github.com/Virtualimmortal/kjvBot/tree/master"
              target="_blank"
              className="btn-floating grey darken-5"
            >
              <i className="fa fa-code"></i>
            </a>
          </li>
          <li>
            <a
              title="Search icons"
              href="#!"
              className="btn-floating blue searchBtn"
            >
              <i className="fa fa-search"></i>
            </a>
          </li>
          <li>
            <a
              title="Scroll to the top of the page"
              href="#!"
              className="btn-floating green toTopBtn"
            >
              <i className="fa fa-arrow-up"></i>
            </a>
          </li>
          <li>
            <a
              title="Scroll to the bottom of the page"
              href="#!"
              className="btn-floating green toBottomBtn"
            >
              <i className="fa fa-arrow-down"></i>
            </a>
          </li>
        </ul>

    </div>
  )
}


/**
 * Initialize page components
 */
document.addEventListener('DOMContentLoaded', function() 
{
  
  /**
   * Floating Action Menu
   */
  var elems = document.querySelectorAll('.fixed-action-btn');
  var fixedActionButtons = M.FloatingActionButton.init(elems, {
    'hoverEnabled': false,
  });

  $('#floatingActionMenu .searchBtn').on('click', function(e) {
    e.preventDefault();
    window.scrollTo(0,0);
    $('#kjvBot').select().focus();
    fixedActionButtons[0].close();
    return false;
  })

  $('.toTopBtn').on('click', function(e) {
    e.preventDefault();
    window.scrollTo(0,0);
    fixedActionButtons[0].close();
    return false;
  })

  $('.toBottomBtn').on('click', function(e) {
    e.preventDefault();
    window.scrollTo(0,document.body.scrollHeight);
    fixedActionButtons[0].close();
    return false;
  })


});
export default FloatingActionMenu
